import React from "react";
import "./navbar.css";
import { ItemInCart } from "../ItemInCart";

const Navbar = () => {
  return (
    <>
      <div className="navBar">
        <div className="navBar_logo">
          <img src="./images/pizza-logo.png" alt="" />
          {/* <h2>PizzaApp</h2> */}
        </div>
        <div className="itemInCart">
          <ItemInCart />
        </div>
      </div>
    </>
  );
};

export default Navbar;
