import React from "react";
import "./products.css";
import { Item } from "./Item";
import { Drinks, Pizza } from "../utils/data";

const Products = () => {
  return (
    <div>
      <h1>Greatest Pizza Ever</h1>
      <div className="pizzaContainer">
        <h3>PIZZA</h3>
        <div className="pizzaItems">
          {Pizza.map((data, key) => {
            return (
              <div key={key}>
                <Item
                  img={data.img}
                  title={data.name}
                  desc={data.desc}
                  price={data.price}
                  //   button={data.button}
                />
              </div>
            );
          })}
        </div>
        <h3>DRINKS</h3>
        <div className="pizzaItems">
          {Drinks.map((data, key) => {
            return (
              <div key={key}>
                <Item
                  img={data.img}
                  title={data.name}
                  desc={data.desc}
                  price={data.price}
                  // button={data.button}
                />
              </div>
            );
          })}
        </div>
        {/*
        <h3>Desserts</h3>
        <div className="pizzaItems">
          {productData.map((data, key) => {
            return (
              <div key={key}>
                <Item
                  img={data.img}
                  title={data.name}
                  desc={data.desc}
                  price={data.price}
                  button={data.button}
                />
              </div>
            );
          })}
        </div> */}
      </div>
    </div>
  );
};

export default Products;
